	position: fixed;
	z-index: 10;
	.logo-tent {
		background-size: contain; 
		height: 35px;
		margin: 0.35rem;
		width: 50px;
		background-image: url("../../images/logos/inhabitent-logo-tent.svg");
	}
	a{
		color: $color__brand-green;
		font-size: 1rem;
		&:visited {
			color: $color__brand-green;
		}
	}
}
